.catalog {
  margin-bottom: 28px;
}

.catalog__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  align-items: center;
  text-transform: uppercase;
  width: 100px;
  margin-bottom: 7px;

  @media (min-width: $tablet) {
    width: 140px;
    text-align: center;
    margin-top:30px;
    margin-left: 92px;
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 24px;
  }

  @media (min-width: $dekstop) {
    line-height: 21px;
    width: 140px;
margin-left: 53px;
margin-top: 39px;
margin-bottom: 15px;
  }
}

.catalog__else-svg-link {
  align-items: center;
  margin: auto;
margin-bottom: 21px;
}
.catalog__else-svg{
  @media(max-width:$mobile){
  margin-left: -10px;}
  @media (min-width: $tablet) {
  width: 60px;
  height: 60px;
margin-left: 130px;
margin-top: 30px;}
@media (min-width: $dekstop) {
margin-left: 94px;
margin-top: 27px;
}
}

.catalog__links {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1;
  display: block;
  width: 280px;
  text-decoration: none;
  color: $basic_white;
  text-transform: uppercase;
  background-color: $basic_green;
  text-align: center;
  padding-top: 12px;
  padding-bottom: 12px;
  margin: auto;
  margin-top: -5px;
  @media (min-width: $tablet) {
    width: 248px;
margin-top: 31px;
padding-bottom: 11px;
margin-left: 40px;
  }
&--margin{
  margin-top: -5px;

  @media (min-width: $tablet) {
    margin-top: 31px;

}
}
&--margin1{
  margin-top: -7px;
  @media (min-width: $tablet) {
    margin-top: 32px;
  }
}
&--margin2 {
  margin-top:-7px;

  @media (min-width: $tablet) {
    margin-top: 33px;
}
}
&--margin3 {
  margin-top:-10px;
  @media (min-width: $tablet) {
    margin-top: 33px;
}
}
&--margin4 {
  margin-top:-5px;
  @media (min-width: $tablet) {
    margin-top: 33px;
}
}
&--margin5 {
  margin-top:-11px;
  margin-bottom: 4px;
  @media (min-width: $tablet) {
    margin-top: 30px;
    margin-left: 38px
}
}


  @media (min-width: $dekstop) {
    line-height: 21px;
    width: 167px;
    padding-top: 9px;
    padding-bottom: 9px;
  }
  &:hover{
    background-color: $basic_green-2
  }
  &:active{
    color:rgba(255, 255, 255, 0.3)
  }
  &:focus{
    background-color: adjust-hue($basic_green-2, 10);
  }
}



.catalog__img-small {
  margin-left: 20px;
  margin-top: 2px;
  &--margin{
    margin-top: 20px;
    margin-left: 26px;
    margin-right: 5px;
    @media (min-width: $tablet) {
      margin-left: 20px;
  }
  }
  &--margin1{
    margin-top: 25px;
    margin-left: 30px;
    margin-right: 10px;
    @media(max-width: $mobile){
    margin-bottom: -2px;}
    @media (min-width: $tablet) {
      margin-left: 20px;
  }
  }
  &--margin3{
    margin-top: 16px;
    margin-left: 25px;
    margin-right: 5px;
      @media (min-width: $tablet) {
        margin-left: 19px;
      }
  }
}

.class__img-big {
  margin: auto;
}

.catalog__div-info {
margin-top: 5px ;

  @media (min-width: $tablet) {
    margin-top: 33px;
  }
  &--margin{
    margin-top: 10px;

    @media (min-width: $tablet) {
      margin-top:-5px;
}
@media (min-width: $dekstop) {
  margin-top:-3px ;
}
}
  &--margin1{
    margin-top: 10px;

    @media (min-width: $tablet) {
      margin-top: 32px;
    margin-left:1px;}
    @media (min-width: $dekstop) {
      margin-left:0px;
    }
  }
  &--margin2 {
    margin-top:9px;

    @media (min-width: $tablet) {
      margin-top: 33px;
}
@media (min-width: $dekstop) {
  margin-top:45px ;
}
  }
  &--margin3 {
    margin-top:9px;
    @media (min-width: $tablet) {
      margin-top: 33px;
}
  }
  &--margin4 {
    margin-top:9px;
    @media (min-width: $tablet) {
      margin-top: 35px;
}
@media (min-width: $dekstop) {
  margin-top:45px ;
}
  }
  &--margin5 {
    margin-top:9px;
    @media (min-width: $tablet) {
      margin-top: 33px;
  }
  }
}

.catalog__card {
  border-top: 1px solid $special_grey-2;
  border-bottom: 1px solid $special_grey-2;

  @media (max-width: $mobile) {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 20px;

  }

  @media (min-width: $tablet) {
    border: none;
    background: $special_grey-2;
    margin-left: 10px;
  }
}

.catalog__else-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #111111;
  margin: auto;
  margin-bottom: 5px;
  margin-left: 49px;
margin-top:-1px;
  @media (min-width: $tablet) {
    width: 160px;
    text-align: center;
    font-size: 20px;
line-height: 22px;
margin-top: 45px;
margin-left: 80px;
  @media (min-width: $dekstop) {
    margin-left: 42px;
    margin-top: 46px;
  }
  }
}

.catalog__else-paragraph {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  color: $special_dark-grey;
  margin: auto;
  margin-left: 37px;

  @media (min-width: $tablet) {
    width: 190px;
    text-align: center;
    font-size: 16px;
line-height: 18px;
margin-top: 22px;
margin-left:65px;
letter-spacing: 0px;
  }
    @media (min-width: $dekstop) {
    margin-top: 22px;
    letter-spacing: 0.1px;
    margin-left: 27px;
    }
}

.catalog__else-link {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1;
  display: block;
  width: 280px;
  text-decoration: none;
  color: $special_dark-grey;
  text-transform: uppercase;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: $basic_grey;
  text-align: center;
  margin: auto;
  margin-top: 22px;
  padding-right: 0px;
  margin-right:20px;

  @media (min-width: $tablet) {
    width: 249px;
    margin-top: 89px;
    margin-left: 38px;
    padding-right: 0px;
  }

  @media (min-width: $dekstop) {
    width: 169px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: 38px;
  }
  &:hover{
    background-color: $special_grey-2;
  }
  &:active{
    color:rgba(0, 0, 0, 0.3);
  }
 &:focus{
      background-color: adjust-hue($special_grey-2, 10);
    }
}

.catalog__else {
  border-bottom: 1px solid $special_grey-2;
  display: flex;
  flex-direction: column;
@media (min-width: $tablet) {
  display: block;
}
  &--margin {
    padding-top: 26px;
    padding-bottom: 25px;
    margin-left: 10px;
  }

  @media (min-width: $tablet) {
    border: 1px solid $special_grey-2;
  }
}

.catalog__table {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  text-align: left;
  width:: 145px;
  color: $special_dark-grey;
  margin-left: -2px;
  margin-top: 12px;
  @media (min-width: $tablet) {
    margin-top:0px;}

&--margin{
    @media (max-width: $mobile) {
  margin-top: 13px;}

  @media (min-width: $tablet) {
    margin-top:0px;

}}
&--margin1{
  @media (max-width: $mobile) {  margin-top: 11px;}

  @media (min-width: $tablet) {
    margin-top: 1px;
  margin-left:-1px;}
}
&--margin2 {
@media (max-width: $mobile) {   margin-top:12px;}

  @media (min-width: $tablet) {
    margin-top: 3px;
margin-left: 0px;
}
@media (min-width: $dekstop) {
margin-top:1px ;
margin-left: -1px;
}
}
&--margin3 {
  @media (max-width: $mobile) {  margin-top:11px;}
  @media (min-width: $tablet) {
    margin-top: 0px;
}
}
&--margin4 {
  margin-top:12px;
  @media (min-width: $tablet) {
    margin-top: 0px;
}
}
&--margin5 {
  margin-top:11px;
  @media (min-width: $tablet) {
    margin-top: 11px;
    margin-bottom: 0px;
}
@media (min-width: $dekstop) {
margin-top:-1px ;
margin-bottom: 3px;
}
}
}

.catalog__links-name {
  text-decoration: none;
  color: $special_dark-grey-2;
}

.catalog__center {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  @media (max-width: $mobile) {
    padding-left: 0;
    padding-right: 0;
  }

  @media (min-width: $tablet) {
    display: grid;
    grid-template-columns: repeat(2, 334px);
    grid-template-rows: repeat(4, 430px);
    grid-column-gap: 50px;
    grid-row-gap: 140px;
    box-sizing: border-box;
  }

  @media (min-width: $dekstop) {
    display: grid;
    grid-template-columns: repeat(4, 255px);
    grid-template-rows: repeat(2, 434px);
    grid-column-gap: 70px;
    grid-row-gap: 140px;
    margin-left: 80px;
    width: 1280px;
    margin-left: auto;
    margin-right: auto;
  }
}

.catalog-page-main__title {
  text-align: center;
}

.catalog__characteristic {
  padding: 0px;
  @media (min-width: $tablet) {
    border-bottom: 1px solid $basic_grey_line;
    margin-left: 37px;
    margin-right: 37px;
    display: flex;
    justify-content: space-between;
    width: 250px;
    font-size: 16px;
line-height: 16px;
padding-bottom: 8px;
padding-top: 7px;
  }

  @media (min-width: $dekstop) {
    width: 165px;
    margin-left: 40px;
    margin-right: 30px;
    font-size: 16px;
    line-height: 16px;
  }
  &--price{
    @media(max-width: $mobile){
      margin-top: -10px;
    }
  }
  }


.catalog__img-size-small {
  @media (min-width: $tablet) {
    width: 315px;
    height: 170px;
    margin-top:-44px;
    margin-left: -17px;
  }

  @media (min-width: $dekstop) {
    width: 245px;
    height: 172px;
    margin-top: -50px;
    margin-left: -21px;
  }
}

.catalog__img-size-big {
  margin-top:10px;
  margin-left: 20px;
  @media (min-width: $tablet) {
    width: 335px;
    height: 210px;
    margin-top:-71px;
    margin-left: -7px;
    margin-bottom: -5px;
  }

  @media (min-width: $dekstop) {
    width: 256px;
    height: 207px;
    margin-top: -78px;
    margin-left: -5px;

  }
  &--size{
    @media (min-width: $tablet) {
    width: 321px;
    height: 201px;
    margin-left: -1px;}
    @media (min-width: $dekstop) {
      width: 249px;
      height: 198px;
      margin-left: -3px;
  }



  }
}
.catalog__table-left{
  text-align: left;
  padding:0px;
}
.catalog__table-right{
  text-align: right;
  padding:0px;
  &--margin1{
    @media (min-width: $tablet) {
      margin-top: 2px;
      margin-right: 5px;
    }
  }
  @media (min-width: $dekstop) {
    margin-right:0px;
  }
}
